.week-1-child {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  z-index: 0;
}
.frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 18px;
  max-width: 100%;
}
.for-todays-lesson,
.the-setting-is {
  margin: 0;
}
.for-todays-lesson-container {
  flex: 1;
  position: relative;
  line-height: 29px;
  display: inline-block;
  max-width: 100%;
  z-index: 2;
}
.for-todays-lesson-we-will-tr-wrapper {
  width: 1027px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 48px 70px;
  box-sizing: border-box;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-gray);
  font-family: var(--font-short-stack);
}
.frame-child,
.frame-item {
  border-radius: var(--br-5xs);
}
.frame-child {
  height: 39px;
  width: 77px;
  position: relative;
  background-color: var(--color-mediumvioletred-200);
  border: 2px solid var(--color-lavender);
  box-sizing: border-box;
  display: none;
  z-index: 0;
}
.frame-item {
  height: 34px;
  width: 73px;
  position: absolute;
  margin: 0 !important;
  top: 1px;
  left: calc(50% - 36.5px);
  background-color: var(--color-mediumvioletred-100);
  z-index: 1;
}
.div {
  align-self: stretch;
  position: relative;
  display: inline-block;
  min-width: 7px;
  z-index: 2;
}
.wrapper {
  width: 7px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-8xs) 0 0;
  box-sizing: border-box;
}
.frame-inner,
.rectangle-div {
  border-radius: var(--br-11xs);
  width: 1px;
  height: 35px;
}
.frame-inner {
  position: relative;
  background-color: var(--color-hotpink);
  display: none;
  z-index: 3;
}
.rectangle-div {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-mediumvioletred-200);
  z-index: 2;
}
.vector-icon {
  position: absolute;
  top: 15px;
  left: 7px;
  width: 10px;
  height: 5px;
  z-index: 2;
}
.rectangle-group {
  height: 35px;
  width: 17px;
  position: relative;
}
.rectangle-parent,
.week-1-inner {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.rectangle-parent {
  height: 39px;
  width: 77px;
  border-radius: var(--br-5xs);
  background-color: var(--color-mediumvioletred-200);
  border: 2px solid var(--color-lavender);
  box-sizing: border-box;
  justify-content: flex-start;
  padding: var(--padding-12xs) var(--padding-6xs) var(--padding-12xs)
    var(--padding-4xl);
  position: relative;
  gap: var(--gap-lgi);
  z-index: 1;
}
.week-1-inner {
  width: 1035px;
  height: 37px;
  justify-content: center;
  max-width: 100%;
}
