.wrapper-rectangle-6-child {
  height: 100%;
  width: 100%;
  max-width: 100%;
  z-index: 2;
  object-fit: contain;
  position: absolute;
  left: 0;
  top: 0;
  transform: scale(1.443);
}
.wrapper-rectangle-6 {
  height: 50px;
  width: 529px;
  position: relative;
  border-radius: var(--br-3xl-1);
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}
.elements-of-story {
  margin: 0 !important;
  width: 100%;
  position: absolute;
  top: 7px;
  left: 0;
  font-size: inherit;
  line-height: 35px;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  z-index: 3;
}
.element-plot,
.element-plot-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.element-plot {
  align-self: stretch;
  flex: 1;
  justify-content: center;
  padding: 0 var(--padding-xl);
  position: relative;
}
.element-plot-wrapper {
  width: 1038px;
  height: 50px;
  justify-content: flex-start;
  padding: 0 var(--padding-8xs);
  text-align: center;
  font-size: var(--font-size-13xl);
}
.our-aim-is-to,
.over-the-next,
.welcome-to-term {
  margin: 0;
}
.welcome-to-term-container {
  flex: 1;
  position: relative;
  letter-spacing: -0.06em;
  line-height: 29px;
  display: inline-block;
  max-width: 100%;
  z-index: 2;
}
.welcome-to-term-2-of-writers-c-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-8xs);
  box-sizing: border-box;
  max-width: 100%;
  color: var(--color-gray);
}
.frame-child80 {
  height: 114px;
  width: 1026px;
  position: relative;
  border-radius: var(--br-8xs);
  background-color: var(--color-lightseagreen);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  border: 3px dashed var(--color-paleturquoise);
  box-sizing: border-box;
  display: none;
  max-width: 100%;
}
.five,
.there-are-elements {
  position: relative;
  line-height: 29px;
  display: inline-block;
}
.there-are-elements {
  align-self: stretch;
  height: 67px;
  white-space: pre-wrap;
  flex-shrink: 0;
  z-index: 2;
}
.five {
  text-shadow: 0.5px 0 0#fff, 0 0.5px 0#fff, -0.5px 0 0#fff, 0-0.5px 0#fff;
  min-width: 52px;
  z-index: 3;
}
.five-wrapper {
  width: 326px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
  margin-top: -66px;
}
.there-are-elements-which-make-parent {
  width: 875px;
  flex-direction: column;
  min-height: 67px;
}
.frame-parent8,
.rectangle-parent5,
.there-are-elements-which-make-parent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.rectangle-parent5 {
  width: 1026px;
  border-radius: var(--br-8xs);
  background-color: var(--color-lightseagreen);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  border: 3px dashed var(--color-paleturquoise);
  box-sizing: border-box;
  flex-direction: row;
  padding: var(--padding-2xs) var(--padding-sm) var(--padding-17xl);
  z-index: 1;
}
.frame-parent8 {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-xs-5);
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-short-stack);
}
@media screen and (max-width: 975px) {
  .elements-of-story {
    font-size: var(--font-size-7xl);
    line-height: 28px;
  }
}
@media screen and (max-width: 450px) {
  .elements-of-story {
    font-size: var(--font-size-lgi);
    line-height: 21px;
  }
  .five,
  .there-are-elements,
  .welcome-to-term-container {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
}
